#product-body {
    height: 800px;
}


#product-info {
    border-bottom: 0.5pt solid;
    width: 100%;
    height: 60%;
}

#product-gallery {
    position: relative;
    width: 45%;
    height: 100%;
    float: left;
}

#gallery-main-img {
    border: 0.25pt solid;
    border-bottom: none;
    width: 100%;
    height: 80%;
}

#gallery-subimg {
    overflow: scroll;
    white-space: nowrap;
    position: relative;
    border-top: 0.25pt solid;
    border-right: 0.25pt solid;
    height: 20%;
}



#gallery-subimg::-webkit-scrollbar {
    display: none;
}

.product-gallery-subitem {
    border-left: 0.25pt solid;
    height: 100%;
    width: 25%;
}

#scroll-button-div {
    width: 100%;
    height: 20%;
    position: absolute;
    left: 0px;
    bottom: 0px;
    z-index: 50px;
}

.scroll-gallery-button {
    height: 100%;
    opacity: 50%;
}

.scroll-gallery-button:hover {
    opacity: 100%;
}

#scroll-gallery-button-left {
    float: left;
}

#scroll-gallery-button-right {
    float: right;
}


#product-description {
    width: 55%;
    height: 35%;
    float: right;
}

#description-text {
    margin-left: 20px;
}

#showAddedText {
    display: inline;
}

#similar-products {
    border-bottom: 0.5pt solid;
    display: flex;
    width: 100%;
    height: 35%;
}
#similar-products-top {
    width: 100%;
}

#prev-item {
    margin-top: 15px;
    margin-right: 5px;
    float: right;
}

#next-item {
    margin-top: 15px;
    margin-right: 10px;
    float: right;
}


#similar-table-row{
    display: flex;
    margin-top: 55px;
    position: absolute;
    left: 0px;
}

.similar-table-link {
    margin-left: 10px;
}

.similar-table-div{
    width: 335px;
    height: 190px;
    padding: 10px;
    border: 1pt solid;
    color: rgba(0, 0, 0, 0.75) !important;
}



