#page-body {
    width: 100%;
    height: 1200px;
    top: 25px;
    margin: auto;
    box-sizing: border-box;
    background-color: white;
}

#searchFilters {
    border-right: 0.25pt solid;
    float: left;
    width: 25%;
    height: 97.5%;
}

#search-filter-heading {
    width: 200px;
}

#clear-filter {
    position: absolute;
    top: 20px;
    left: 230px;
}

#filter-text {
    margin-top:10px;
    margin-left: 15px;
}

#sort-options {
    position: absolute;
    top: 30px;
    right: -5px;
    z-index: 100px;
    width: 200px;
    height: 200px;
}

.category-filter {
    cursor: pointer;
}

.brands-filter {
    cursor: pointer;
}

.price-filter {
    cursor: pointer;
}



#az {
    width: 150px;
}

#za {
    width: 150px;
    
}

#hl {
    width: 150px;
    
}

#lh {
    width: 150px;
}

#show-results {
    text-align: center;
    height: 50px;
    width: 75%;
}

#sort-by {
    margin-right: 20px;
    float: right;
}

#displayResultsTable {
    float: right;
    width: 75%;
    height: 95%;
}

#displayResultsTable .table-row{
    display: flex;
    margin-top: 10px;
}

#displayResultsTable .table-division{
    display: flex;
    margin-left: 10px;
    width: 335px;
    height: 250px;
    padding: 10px;
    border: 1.5pt solid;
    color: black !important;
}

#no-result {
    margin-left: 10%;
    margin-top: 5%;
    font-size: 20px;
}

#prev-page {
    float: right;
}

#next-page {
    float: right;
}

