html::-webkit-scrollbar {
    display: none;
}

#homepage-body
{
    display: inline-block;
    position: relative;
    width: 100%;
    height: 1000px;
    margin: auto;
    box-sizing: border-box;
    background-color: white;
}

.newsletter
{
    box-sizing: border-box;
    position: absolute;
    background-color: aqua;
    left: 0px;
    bottom:45px;
    margin: auto;
    height: 170px;
    width: 100%;
}

#detail
{
    padding: 10px;
    padding-left: 30px;
    font-size: 40px;
}

#news_entry
{
    margin-left: 50px;
    display: inline-block;
    width: 20%;
    padding: 10px;
    border-radius: 5px;
}

#subscribe
{
    display: inline-block;
    padding: 10px;
    border-radius: 5px;
    margin-left: 10px;
}

#text
{
    display: inline-block;
    font-size: 20px;
}

#detail2
{
    padding-left: 35px;
}