.searchInput
{
    display: flex;
}

.search input {
    background-color: white;
    border: 0;
    font-size: 18px;
    height: 40px;
    width: 300px;
}

.searchIcon
{
    display: flex;
    height: 40px;
    width: 35px;
    background-color: white;
}

.searchIcon img
{
    width: 35px;
    height: 35px;
}

input:focus {
    outline: none;
}
.dataResult {
    margin-top: 5px;
    width: 300px;
    z-index: 500px;
    position: absolute;
    background-color: white;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    overflow: hidden;
    overflow-y: auto;
}

.dataResult::-webkit-scrollbar {
    display: none;
}

.dataResult .dataItem {
    width: 100%;
    height: 40px;
    display: flex;
    
    align-items: center;
    color: black;
}

.dataItem p {
    margin-left: 10px;
}

a:link, a:hover, a:active, a:visited {
    text-decoration: none !important;
}

a:hover {
    background-color: lightgray;
}

#clearbtn {
    cursor: pointer;
}