#cart-body {
    height: 800px;
}

#cart-body-div {
    margin-left:auto;
    margin-right: auto;
    top: 10%;
    height: 90%;
    width: 60%;
    border: 1pt solid;
}

#cart-top-text {
    width: 100%;
    margin-top: 20px;
    margin-left: 10%;
    margin-bottom: 20px;
    text-align: center;
}

#clear-cart {
    float: right;
    margin-right: 21%;
}

.cart-body-item {
    margin-top: 10px;
    margin-left: auto;
    margin-right:auto;
    width: 80%;
    height: 20%;
    border: 1pt solid;
}