header
{
    box-sizing: border-box;
    position: fixed;
    z-index: 10;
    margin: auto;
    top: 0;
    left: 0;
    width: 100%;
    background-color: grey;
    padding: 10px;
}

.logo
{
    display: inline-block;
    width: 42px;
    height: 42px;
}

.logo img
{
    border: 2px;
    width: 40px;
    height: 40px;
}

.Searchbar
{
    display: inline-block;
    margin-left: 30%;
    margin-right: 25%;
}

#log
{
    display: inline;
}
.btn, .btn-primary
{
    margin-top: 3px !important;
    margin-left: 5px !important;
    margin-right: 5px !important;
    border-color: rgb(11, 21, 134) !important;
    background-color: white !important;
    color: rgb(11, 21, 134) !important;
    float: right !important;
}

#sup {
    position: absolute;
    background-color: green;
    z-index: 1000px;
    left: 0;
    top:0;
    margin-top: 8%;
    margin-left: 35%;
    margin-right: 25%;
    display: none;
}

#lin {
    position: absolute;
    background-color: green;
    z-index: 1000px;
    left: 0;
    top:0;
    margin-top: 8%;
    margin-left: 35%;
    margin-right: 25%;
    display: none;
}
